	methods:{
		Loadthepage() {
			var self = this;
			if(!self.loadshow){
				return
			}
			if (self.page < self.pages) {
				var page = self.page + 1;
				service.auth(self, service.api.record.stock_log, "POST", {
					page: page,
					limit: 15
				}, function(self, res) {
					console.log(res)
					self.pages = res.data.page_total;
					self.page = page;
					// self.glory = res.data.glory;
					var list = res.data.data;
					list.forEach((v, i) => {
						self.scrlist.push(v);
					});
					if (self.pages == self.page || self.scrlist.length == 0) {
						self.loadshow = false
						uni.showToast({
							title: '数据已加载完毕',
							icon: 'none'
						});
					} else {
						self.loadshow = true
					}
				});
			} else {
				self.loadshow = false
				uni.showToast({
					title: '数据已加载完毕',
					icon: 'none'
				});
			}
		},
		lower(){
			this.Loadthepage();
		}
	},
	onShow() {
		this.Loadthepage();
		service.auth(this, service.api.record.stock_total, "POST", '' , function(self, res) {
			self.stockss = res.stock_total;
			console.log(self.stockss )
		});
	}
